import React from 'react'
import classes from './index.module.scss'
import EditIcon from '@material-ui/icons/Edit'
import TextField from '@material-ui/core/TextField'
import FormHelperText from '@material-ui/core/FormHelperText'

const ChangeName = props => {
  const { isEdit, setIsEdit, HandleChange, form, error } = props

  if (isEdit) {
    return (
      <div id='changeName' className={classes.changeName}>
        <div id='editIcon' className={classes.editIcon}>
          <EditIcon
            onClick={() => {
              setIsEdit(false)
            }}
          />
        </div>
        <div id='textFields' className={classes.textFields}>
          <TextField
            id='inputName'
            className={`${classes.TextField} ${error.last_name && classes.hasError}`}
            autoComplete='off'
            placeholder='Last Name'
            name={'last_name'}
            value={form.last_name}
            onChange={HandleChange}
          />
          { error && <FormHelperText id='error' className={classes.nameError} > {error.last_name} </FormHelperText>}
        </div>
        <div id='textFields' className={classes.textFields}>
          <TextField
            id='inputName'
            className={`${classes.TextField} ${error.first_name && classes.hasError}`}
            autoComplete='off'
            name={'first_name'}
            placeholder='First Name'
            value={form.first_name}
            onChange={HandleChange}
          />
          { error && <FormHelperText id='error' className={classes.nameError} > {error.first_name} </FormHelperText>}
        </div>
        <TextField
          id='inputName'
          className={classes.TextField}
          autoComplete='off'
          placeholder='Suffix'
          name={'suffix'}
          value={form.suffix.trim()}
          onChange={HandleChange}
        />
        <div id='textFields' className={classes.textFields} >
          <TextField
            id='inputName'
            className={`${classes.TextField} ${error.middle_name && classes.hasError}`}
            autoComplete='off'
            placeholder='Middle Name'
            name={'middle_name'}
            value={form.middle_name}
            onChange={HandleChange}
          />
          { error && <FormHelperText id='error' className={classes.nameError} > {error.middle_name} </FormHelperText>}
        </div>
      </div>
    )
  } else {
    return (
      <div id='changeName' className={classes.changeName}>
        <div id='editIcon' className={classes.editIcon}>
          <EditIcon
            onClick={() => {
              setIsEdit(true)
            }}
          />
        </div>
        {`${form['last_name']} ${form['first_name']} ${form['suffix']} ${form['middle_name']}`}
      </div>
    )
  }
}

export default ChangeName
