import React from 'react'
import { TableRow, TableCell, Button, Paper } from '@material-ui/core'
import InputData from '../Inputs/inputData'
import classes from './index.module.scss'

const DataComponents = props => {
  const { dispatch = null, setUpdate = null, stateValue = {} } = props

  const { GetKeys = [], tableDataProps = '' } = props

  const handleChange = React.useCallback((e, index) => {
    const { name, value } = e.target

    dispatch(setUpdate({
      name,
      value,
      index
    }))
  }, [])

  return (
    <React.Fragment>
      {
        tableDataProps.map((bodyItems, index) => {
          const keys = GetKeys()
          const dataIndex = index
          return (
            <TableRow key={index} className={classes.TableRow}>
              {
                keys.map((items, index) => {
                  return (
                    <TableCell key={`${index}-cell`}>
                      {stateValue.isEdit ? items.editable
                        ? <InputData
                          name={items.id}
                          value={bodyItems[items.id]}
                          onChange={e => handleChange(e, dataIndex)}
                          size={items.size}
                          inputType={items.inputType}
                          type={items.type}
                          multiline={items.multiline}
                        />
                        : <div style={{ width: items.size ? items.size : 'auto', height: 'auto', textTransform: 'capitalize' }}>
                          {bodyItems[items.id]}
                        </div>
                        : <div style={{ width: items.size ? items.size : 'auto', height: 'auto', textTransform: 'capitalize' }}>
                          {bodyItems[items.id]}
                        </div>
                      }
                    </TableCell>

                  )
                })
              }

            </TableRow>
          )
        })
      }

    </React.Fragment>
  )
}

export default React.memo(DataComponents)
