import React from 'react'
import { Paper } from '@material-ui/core'
import StatusSelect from '../SelectInput'
import InputFields from '../InputField'

const InputData = props => {
  const { name = '', value = '', onChange, size = '', inputType = '', type = '', multiline = null } = props
  return (
    <div style={{ width: size || 'auto', height: 'auto' }}>
      <Paper>
        { inputType === 'dropDown'
          ? <StatusSelect
            name={name}
            value={value}
            onChange={onChange}
          />
          : <InputFields
            {...props}
            type={type}
            multiline={multiline}
          />
        }

      </Paper>
    </div>

  )
}

export default React.memo(InputData, (prevProps, nextProps) => {
  try {
    if (prevProps.name === nextProps && prevProps.value === nextProps.value && prevProps.onChange === nextProps.onChange) {
      return true
    }
    return false
  } catch (e) {
    return false
  }
})
